import {Component, OnInit} from '@angular/core';
import {ValidateService} from '../../services/validate.service';
import {NgFlashMessageService} from "ng-flash-messages";
import {AuthService} from "../../services/auth.service";
import {Router} from "@angular/router";

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {

  name: string;
  username: string;
  email: string;
  password: string;

  constructor(private validateService: ValidateService, private flashMessagesService: NgFlashMessageService, private authService: AuthService, private router: Router) {
  }

  ngOnInit() {
  }

  onRegisterSubmit() {
    const user = {
      name: this.name,
      username: this.username,
      email: this.email,
      password: this.password
    }

    if (!this.validateService.validateRegister(user)) {
      this.flashMessagesService.showFlashMessage({
        messages: ['Please fill in all fields'],
        dismissible: true,
        type: 'danger',
        timeout: 3000
      });
      return false;
    }
    if (!this.validateService.validateEmail(user.email)) {
      this.flashMessagesService.showFlashMessage({
        messages: ['Please fill in valid email'],
        dismissible: true,
        type: 'danger',
        timeout: 3000
      });
      return false;
    }

    this.authService.registerUser(user).subscribe( (data) => {
      if(data){
        this.flashMessagesService.showFlashMessage({
          messages: ['You are now registered and can log in'],
          dismissible: true,
          type: 'success',
          timeout: 3000
        });
        this.router.navigate(['/login']);
      }else{
        this.flashMessagesService.showFlashMessage({
          messages: ['something went wrong'],
          dismissible: true,
          type: 'danger',
          timeout: 3000
        });
        this.router.navigate(['/register']);
      }
    })
  }
}
